"use client";

import { useState, useEffect } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import {
  getCategoriesByStore,
  searchProductsByName,
  getProductsByCategory,
  compareProductsAcrossStores,
} from "@/lib/database";
import { Category, Product } from "@/lib/supabase";

export default function ComproAppUI() {
  const [activeMethod, setActiveMethod] = useState("searchByStore");
  const [selectedStore, setSelectedStore] = useState("Bayinet");
  const [searchMethod, setSearchMethod] = useState("searchByName");
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("");

  // Supabase state'leri
  const [categories, setCategories] = useState<Category[]>([]);
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Kategorileri y√ºkle
  const loadCategories = async (store: string) => {
    setLoading(true);
    setError(null);
    try {
      const data = await getCategoriesByStore(store);
      setCategories(data);
    } catch (err) {
      setError("Kategoriler y√ºklenirken hata olu≈ütu");
      console.error("Error loading categories:", err);
    } finally {
      setLoading(false);
    }
  };

  // Maƒüaza deƒüi≈ütiƒüinde kategorileri y√ºkle
  useEffect(() => {
    loadCategories(selectedStore);
  }, [selectedStore]);

  // √úr√ºn arama fonksiyonu
  const handleSearch = async () => {
    if (!searchTerm.trim()) return;

    setLoading(true);
    setError(null);
    try {
      let data: Product[] = [];
      if (activeMethod === "compare") {
        data = await compareProductsAcrossStores(searchTerm);
      } else {
        data = await searchProductsByName(searchTerm, selectedStore);
      }
      setProducts(data);
    } catch (err) {
      setError("√úr√ºnler aranƒ±rken hata olu≈ütu");
      console.error("Error searching products:", err);
    } finally {
      setLoading(false);
    }
  };

  // Kategori se√ßimi fonksiyonu
  const handleCategorySelect = async () => {
    if (!selectedCategory) return;

    setLoading(true);
    setError(null);
    try {
      // Oksid i√ßin kategori adƒ±nƒ± kullan, diƒüerleri i√ßin ID kullan
      let categoryIdentifier = selectedCategory;
      if (selectedStore.toLowerCase() === "oksid") {
        // Oksid i√ßin se√ßilen category value'si zaten kategori adƒ±
        const selectedCat = categories.find(
          (cat) => cat.name === selectedCategory
        );
        categoryIdentifier = selectedCat ? selectedCat.name : selectedCategory;
      }

      const data = await getProductsByCategory(
        categoryIdentifier,
        selectedStore
      );
      setProducts(data);
    } catch (err) {
      setError("Kategori √ºr√ºnleri y√ºklenirken hata olu≈ütu");
      console.error("Error loading category products:", err);
    } finally {
      setLoading(false);
    }
  };

  // G√ºncelleme butonlarƒ± i√ßin fonksiyonlar
  const handleBayinetUpdate = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch("/api/update", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ store: "bayinet" }),
      });

      const result = await response.json();

      if (response.ok) {
        alert("‚úÖ Bayinet g√ºncelleme ba≈ülatƒ±ldƒ±! Konsolu kontrol edin.");
        console.log("Bayinet g√ºncelleme:", result);
      } else {
        throw new Error(result.error || "G√ºncelleme ba≈ülatƒ±lamadƒ±");
      }
    } catch (error) {
      console.error("Bayinet g√ºncelleme hatasƒ±:", error);
      setError(
        "Bayinet g√ºncelleme sƒ±rasƒ±nda hata olu≈ütu: " + (error as Error).message
      );
    } finally {
      setLoading(false);
    }
  };

  const handleOksidUpdate = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch("/api/update", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ store: "oksid" }),
      });

      const result = await response.json();

      if (response.ok) {
        alert("‚úÖ Oksid g√ºncelleme ba≈ülatƒ±ldƒ±! Konsolu kontrol edin.");
        console.log("Oksid g√ºncelleme:", result);
      } else {
        throw new Error(result.error || "G√ºncelleme ba≈ülatƒ±lamadƒ±");
      }
    } catch (error) {
      console.error("Oksid g√ºncelleme hatasƒ±:", error);
      setError(
        "Oksid g√ºncelleme sƒ±rasƒ±nda hata olu≈ütu: " + (error as Error).message
      );
    } finally {
      setLoading(false);
    }
  };

  const handleDengeUpdate = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch("/api/update", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ store: "denge" }),
      });

      const result = await response.json();

      if (response.ok) {
        alert("‚úÖ Denge g√ºncelleme ba≈ülatƒ±ldƒ±! Konsolu kontrol edin.");
        console.log("Denge g√ºncelleme:", result);
      } else {
        throw new Error(result.error || "G√ºncelleme ba≈ülatƒ±lamadƒ±");
      }
    } catch (error) {
      console.error("Denge g√ºncelleme hatasƒ±:", error);
      setError(
        "Denge g√ºncelleme sƒ±rasƒ±nda hata olu≈ütu: " + (error as Error).message
      );
    } finally {
      setLoading(false);
    }
  };

  // This would be your dummy data for categories (sadece Oksid dƒ±≈üƒ± i√ßin kullanƒ±lƒ±yor)
  const bayinetCategories = [
    { id: "01", name: "Bilgisayar Bile≈üenleri" },
    { id: "02", name: "Ki≈üisel Bilgisayar" },
    { id: "10", name: "Aƒü √úr√ºnleri" },
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-black p-8">
      <div className="max-w-6xl mx-auto">
        <h1 className="text-4xl font-bold text-white mb-8 text-center">
          ‚ú® ComPro √úr√ºn Arama
        </h1>

        {/* G√ºncelleme Butonlarƒ± */}
        <div className="mb-8 p-6 bg-gray-800/50 backdrop-blur-sm rounded-xl shadow-2xl border border-gray-600">
          <h2 className="text-xl font-semibold text-white mb-4 text-center">
            üîÑ Veritabanƒ± G√ºncellemeleri
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Button
              onClick={handleBayinetUpdate}
              disabled={loading}
              className="bg-red-600 hover:bg-red-700 text-white font-semibold py-3 px-6 rounded-lg transition-colors shadow-lg"
            >
              {loading ? "‚è≥ G√ºncelleniyor..." : "üîÑ Bayinet G√ºncelle"}
            </Button>
            <Button
              onClick={handleOksidUpdate}
              disabled={loading}
              className="bg-orange-600 hover:bg-orange-700 text-white font-semibold py-3 px-6 rounded-lg transition-colors shadow-lg"
            >
              {loading ? "‚è≥ G√ºncelleniyor..." : "üîÑ Oksid G√ºncelle"}
            </Button>
            <Button
              onClick={handleDengeUpdate}
              disabled={loading}
              className="bg-gray-900 hover:bg-black text-white font-semibold py-3 px-6 rounded-lg transition-colors shadow-lg border border-gray-600"
            >
              {loading ? "‚è≥ G√ºncelleniyor..." : "üîÑ Denge G√ºncelle"}
            </Button>
          </div>
        </div>

        <Tabs
          defaultValue="searchByStore"
          onValueChange={setActiveMethod}
          className="w-full"
        >
          <TabsList className="grid w-full grid-cols-2 bg-gray-800/50 border-gray-600 text-white">
            <TabsTrigger
              value="searchByStore"
              className="data-[state=active]:bg-gray-700 data-[state=active]:text-white text-gray-300"
            >
              üîç Maƒüazaya G√∂re Ara
            </TabsTrigger>
            <TabsTrigger
              value="compare"
              className="data-[state=active]:bg-gray-700 data-[state=active]:text-white text-gray-300"
            >
              üÜö Kar≈üƒ±la≈ütƒ±r
            </TabsTrigger>
          </TabsList>

          <TabsContent value="searchByStore" className="mt-6">
            <div className="p-6 bg-gray-800/50 backdrop-blur-sm rounded-xl shadow-2xl border border-gray-600">
              <h2 className="text-xl font-semibold text-white mb-4">
                üõí Maƒüaza Se√ßin
              </h2>
              <RadioGroup
                value={selectedStore}
                onValueChange={setSelectedStore}
                className="flex space-x-4 mb-6"
              >
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="Bayinet" id="r1" />
                  <Label htmlFor="r1" className="text-white">
                    Bayinet
                  </Label>
                </div>
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="Denge" id="r2" />
                  <Label htmlFor="r2" className="text-white">
                    Denge
                  </Label>
                </div>
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="Oksid" id="r3" />
                  <Label htmlFor="r3" className="text-white">
                    Oksid
                  </Label>
                </div>
              </RadioGroup>

              <h2 className="text-xl font-semibold text-white mb-4">
                Arama Y√∂ntemi
              </h2>
              <RadioGroup
                value={searchMethod}
                onValueChange={setSearchMethod}
                className="flex space-x-4 mb-6"
              >
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="searchByName" id="sm1" />
                  <Label htmlFor="sm1" className="text-white">
                    üîç ƒ∞simle Ara
                  </Label>
                </div>
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="searchByCategory" id="sm2" />
                  <Label htmlFor="sm2" className="text-white">
                    üìÇ Kategoriye G√∂re
                  </Label>
                </div>
              </RadioGroup>

              {searchMethod === "searchByName" ? (
                <div>
                  <Label
                    htmlFor="search-term"
                    className="block text-white mb-2"
                  >
                    √úr√ºn Adƒ± veya Anahtar Kelime:
                  </Label>
                  <Input
                    id="search-term"
                    type="text"
                    placeholder="√ñrn: 'RX 6600' veya 'Lenovo Legion'"
                    value={searchTerm}
                    onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                      setSearchTerm(e.target.value)
                    }
                    className="mb-4"
                  />
                  <Button
                    className="w-full"
                    onClick={handleSearch}
                    disabled={loading}
                  >
                    {loading ? "Araniyor..." : "Ara"}
                  </Button>
                </div>
              ) : (
                <div>
                  <Label
                    htmlFor="category-select"
                    className="block text-white mb-2"
                  >
                    Kategori Se√ßin:
                  </Label>
                  <select
                    id="category-select"
                    value={selectedCategory}
                    onChange={(e) => setSelectedCategory(e.target.value)}
                    className="w-full p-2 bg-gray-700 border border-gray-600 text-white rounded-md mb-4 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    disabled={loading}
                  >
                    <option value="">-- Kategori Se√ßiniz --</option>
                    {categories.map((cat) => (
                      <option
                        key={cat.id}
                        value={
                          selectedStore.toLowerCase() === "oksid"
                            ? cat.name
                            : cat.id
                        }
                      >
                        {cat.name}
                      </option>
                    ))}
                  </select>
                  <Button
                    className="w-full"
                    onClick={handleCategorySelect}
                    disabled={loading || !selectedCategory}
                  >
                    {loading ? "Y√ºkleniyor..." : "Listele"}
                  </Button>
                </div>
              )}
            </div>
          </TabsContent>

          <TabsContent value="compare" className="mt-6">
            <div className="p-6 bg-gray-800/50 backdrop-blur-sm rounded-xl shadow-2xl border border-gray-600">
              <h2 className="text-xl font-semibold text-white mb-4">
                üÜö √úr√ºn Kar≈üƒ±la≈ütƒ±r
              </h2>
              <Label htmlFor="compare-term" className="block text-white mb-2">
                Kar≈üƒ±la≈ütƒ±rƒ±lacak √úr√ºn Adƒ±:
              </Label>
              <Input
                id="compare-term"
                type="text"
                placeholder="√ñrn: 'RTX 3060'"
                value={searchTerm}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                  setSearchTerm(e.target.value)
                }
                className="mb-4"
              />
              <Button
                className="w-full"
                onClick={handleSearch}
                disabled={loading}
              >
                {loading ? "Kar≈üƒ±la≈ütƒ±rƒ±lƒ±yor..." : "Kar≈üƒ±la≈ütƒ±r"}
              </Button>
            </div>
          </TabsContent>
        </Tabs>

        {/* Hata mesajlarƒ± */}
        {error && (
          <div className="mt-6 p-4 bg-red-900/80 border border-red-600 text-red-200 rounded-lg backdrop-blur-sm">
            ‚ùå {error}
          </div>
        )}

        {/* √úr√ºn sonu√ßlarƒ± */}
        {products.length > 0 && (
          <div className="mt-6 p-6 bg-gray-800/50 backdrop-blur-sm rounded-xl shadow-2xl border border-gray-600">
            <h3 className="text-xl font-semibold text-white mb-4">
              üì¶ Bulunan √úr√ºnler ({products.length})
            </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {products.map((product) => (
                <div
                  key={product.id}
                  className="border border-gray-600 bg-gray-700/50 rounded-lg p-4 hover:shadow-xl hover:bg-gray-600/50 transition-all duration-300"
                >
                  {product.image_url && (
                    <img
                      src={product.image_url}
                      alt={product.name}
                      className="w-full h-32 object-cover rounded-md mb-3"
                    />
                  )}
                  <h4 className="font-semibold text-white mb-2 line-clamp-2">
                    {product.name}
                  </h4>
                  <div className="text-sm text-gray-300 space-y-1">
                    <p>
                      üè™{" "}
                      <span className="font-medium text-white">
                        {product.store}
                      </span>
                    </p>
                    {product.price && (
                      <p>
                        üí∞{" "}
                        <span className="font-bold text-green-400">
                          {product.price.toLocaleString("tr-TR")} ‚Ç∫
                        </span>
                      </p>
                    )}
                    {product.stock_status && (
                      <p>
                        üì¶{" "}
                        <span
                          className={
                            product.stock_status === "in_stock"
                              ? "text-green-400"
                              : "text-red-400"
                          }
                        >
                          {product.stock_status === "in_stock"
                            ? "Stokta"
                            : "Stok Yok"}
                        </span>
                      </p>
                    )}
                  </div>
                  {product.description && (
                    <p className="text-xs text-gray-400 mt-2 line-clamp-2">
                      {product.description}
                    </p>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Loading durumunda bo≈ü sonu√ß mesajƒ± */}
        {!loading && products.length === 0 && searchTerm && (
          <div className="mt-6 p-4 bg-yellow-900/80 border border-yellow-600 text-yellow-200 rounded-lg text-center backdrop-blur-sm">
            üîç "{searchTerm}" i√ßin √ºr√ºn bulunamadƒ±
          </div>
        )}
      </div>
    </div>
  );
}
